import React from 'react';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';
import 'bootswatch/dist/sandstone/bootstrap.min.css';
import Home from './Home';
import Dashboard from './Dashboard.js';
import Login from './Login.js';
import PrivateRoute from './Utils/PrivateRoute.js';
import PublicRoute from './Utils/PublicRoute.js';
import { getUser, removeUserSession } from './Utils/Common';

function App() {
  var logButton = "";

  const handleLogout = () => {
    removeUserSession();
    window.location.reload();
  }

  if (getUser() === null) logButton = <Link to={'/login'} className="nav-link">Kirjaudu</Link>;
  else if (getUser() !== null) {
    logButton = <Link to={'/'} className="nav-link" onClick={handleLogout}>Kirjaudu ulos</Link>;
  }

  return (
    <div className="App">
      <Router>
        <nav className="navbar navbar-expand-lg navbar-dark bg-primary mb-4">
          <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarColor03" aria-controls="navbarColor03" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarColor03">
            <ul className="navbar-nav mr-auto">
              <li className="nav-item"><Link to={'/'} className="nav-link">Etusivu</Link></li>
              <li className="nav-item"><Link to={'/omatsivut'} className="nav-link">Omat sivut</Link></li>
              <li className="nav-item">{logButton}</li>
            </ul>
          </div>
        </nav>
        <Switch>
          <Route exact path="/" component={Home} />
          <PrivateRoute path="/omatsivut" component={Dashboard} />
          <PublicRoute path="/login" component={Login} />
        </Switch>
      </Router>

    </div>
  );
}

export default App;
