import React from 'react'
import { getUser } from './Utils/Common';
import axios from 'axios';


export default class Dashboard extends React.Component {
    constructor(props) {
        super(props);
        this.state = { laput: [], noteToEdit: [] };
        this.deleteFunktio = this.deleteFunktio.bind(this);
    }
    componentDidMount() {
        this.getNotes();
    }
    deleteFunktio(id) {

        axios.post('/proj_api/notes.php', { req: 'delete', noteid: id })
        .then(response => { window.location.reload(); }) 
        .catch(error => {
            console.log(error);
        });
    }
    getNotes() {

        const user = getUser();
        //const token = getToken();
        axios.post('/proj_api/notes.php', { req: 'getnotes', userid: user.userId })
        .then(response => {
            const laput = response.data;
            this.setState({ laput });
        }).catch(error => {
          
        });
    }

    render() {
        const user = getUser();
        const style = { margin: "5px" };
        var pvmForm = { year: 'numeric', month: 'numeric', day: 'numeric' }; // weekday: 'long',, hour:'2-digit', minute:'2-digit' 
        return (

            <div className="container">
                <h4>Moi {user.username}!</h4>
                <hr />
                <button type="button" className="btn btn-outline-success" data-toggle="modal" data-target="#lisaaModal" style={style}>Lisää uusi</button><LisaaModal />
                <button type="button" className="btn btn-outline-info" data-toggle="modal" data-target="#helpModal">Helppi</button><HelpModal />
                <hr />
                <div className="container row">
                    {this.state.laput.map(item =>
                        <div className="card bg-light mb-3" style={{ maxWidth: "20rem", margin: "5px" }} key={item.noteid}>
                            <div className="card-header">{(new Date(item.luotu)).toLocaleDateString('fi-FI', pvmForm)}</div>
                            <div className="card-body">
                                <h4 className="card-title">{item.otsikko}</h4>
                                <p className="card-text">{item.teksti}</p>
                                <hr />
                                <button style={style} className="btn btn-outline-warning btn-sm" id="poista" onClick={() => this.setState({ noteToEdit: item })} data-toggle="modal" data-target='#poistaModal'>Poista</button>
                                <button style={style} className="btn btn-outline-success btn-sm" id="muokkaa" onClick={() => this.setState({ noteToEdit: item })} data-toggle="modal" data-target='#muokkaaModal'>Muokkaa</button>
                                <MuokkaaModal noteToEdit={this.state.noteToEdit} />
                                <PoistaModal noteToEdit={this.state.noteToEdit} deleteFunktio={this.deleteFunktio} />
                            </div>
                        </div>
                    )}
                </div>
            </div>
        );
    }
}
// TODO: fix: click muokkaa -> poistu -> muokkaa samaa niin muokkaus/teksti näkyy samassa ja muissa korteissa  :(( 
// nyt hätäratkaisu:  onClick={() => window.location.reload()} poistu-napissa
class MuokkaaModal extends React.Component {
    constructor(props) {
        super(props);
        this.state = { noteToEdit: [], otsikko: '', teksti: '' }
        this.handleInputChange = this.handleInputChange.bind(this);
    }

    handleClickTallenna(ev) {

        ev.preventDefault();
        let ots = this.state.otsikko;
        let text = this.state.teksti;
        if (ots === '') ots = this.props.noteToEdit.otsikko;
        if (text === '') text = this.props.noteToEdit.teksti;

        this.saveEdit(ots, text);

    }
    saveEdit(ots, teksti) {

        const noteJson = JSON.stringify({
            req: 'edit',
            noteid: this.props.noteToEdit.noteid,
            otsikko: ots,
            teksti: teksti
        });
        
        fetch('/proj_api/notes.php', {
            method: "POST",
            headers: {
                "Accept": "application/json",
                "Content-Type": "application/json"
            },
            body: noteJson
        }).then((res) => res.json())
            .then((json) => {
                const success = json;
                if (success) {
                    window.location.reload();
                }
            });
    }
    handleInputChange(ev) {
        this.setState({ ...this.state, [ev.target.id]: ev.target.value })
    }

    render() {

        const fieldstyle = { border: "1px solid LightGray", padding: "3px", borderRadius: "5px", outline: "none", margin: "5px" };
        return (
            <div className="modal fade" id="muokkaaModal" tabIndex="-1" role="dialog" aria-labelledby="ModalLabel" aria-hidden="true">
                <div className="modal-dialog" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title" id="ModalLabel">Muokkaa muistilappua</h5>
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <form onSubmit={this.handleClickTallenna.bind(this)}>
                            <div className="modal-body">
                                <div className="col-sm-10">
                                    <input type="text" className="form-control-plaintext" id="otsikko" onChange={this.handleInputChange} defaultValue={this.props.noteToEdit.otsikko} style={fieldstyle} />
                                    <textarea className="form-control" id="teksti" rows="5" onChange={this.handleInputChange} style={fieldstyle} defaultValue={this.props.noteToEdit.teksti} ></textarea>
                                </div>
                            </div>
                            <div className="modal-footer">
                                <button type="reset" className="btn btn-outline-warning btn-lg" onClick={() => window.location.reload()}>Poistu</button>
                                <button type="submit" className="btn btn-outline-success btn-lg" >Tallenna</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        )
    }
}

class PoistaModal extends React.Component {

    handleClickDelete() {
        this.props.deleteFunktio(this.props.noteToEdit.noteid);
    }
    render() {
        return (
            <div className="modal fade" id="poistaModal" tabIndex="-1" role="dialog" aria-labelledby="ModalLabel" aria-hidden="true">
                <div className="modal-dialog" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title" id="ModalLabel">Poista</h5>
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            <p>Haluatko poistaa muistiinpanon "{this.props.noteToEdit.otsikko}"?</p>
                        </div>
                        <div className="modal-footer">
                            <button onClick={this.handleClickDelete.bind(this)} type="button" className="btn btn-outline-warning" data-dismiss="modal">Poista</button>
                            <button type="button" className="btn btn-outline-success" data-dismiss="modal">Peruuta</button>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

class LisaaModal extends React.Component {
    constructor(props) {
        super(props);
        const user = getUser();
        this.state = { otsikko: '', teksti: '', userId: user.userId }
        this.handleInputChange = this.handleInputChange.bind(this);
    }
    handleInputChange(ev) {
        this.setState({ ...this.state, [ev.target.id]: ev.target.value })
    }
    handleSubmit(ev) {

        if (this.state.teksti === '' || this.state.otsikko === '') {
            ev.preventDefault();
            alert("Anna muistiinpanoillesi sisältö ja otsikko!");
        }
        else {
            ev.preventDefault();
            const mpanoJson = JSON.stringify({
                req: 'addnote',
                userid: parseInt(this.state.userId),
                otsikko: this.state.otsikko,
                teksti: this.state.teksti
            });
            console.log(mpanoJson);
            fetch('/proj_api/notes.php', {
                method: "POST",
                headers: {
                    "Accept": "application/json",
                    "Content-Type": "application/json"
                },
                body: mpanoJson
            }).then(response => {
                window.location.reload();
            })
                .catch(error => {
                    ev.preventDefault();
                    console.log(error);
                })
        }
    }
    handlePoistu() {
        document.getElementById('teksti').value = '';
        document.getElementById('otsikko').value = '';
    }
    render() {


        const fieldstyle = { border: "1px solid LightGray", padding: "3px", borderRadius: "5px", outline: "none", margin: "5px" };
        return (
            <div className="modal fade" id="lisaaModal" tabIndex="-1" role="dialog" aria-labelledby="ModalLabel" aria-hidden="true">
                <div className="modal-dialog" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title" id="ModalLabel">Lisää uusi muistilappu</h5>
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <form>
                            <div className="modal-body">
                                <div className="col-sm-10">
                                    <input type="text" className="form-control-plaintext" id="otsikko" onChange={this.handleInputChange} placeholder="Anna otsikko" style={fieldstyle} />
                                    <textarea className="form-control" id="teksti" onChange={this.handleInputChange} rows="5" style={fieldstyle} placeholder="Kirjoita tähän muistiinpanosi" ></textarea>
                                </div>
                            </div>
                            <div className="modal-footer">
                                <button type="reset" className="btn btn-outline-warning btn-lg" data-dismiss="modal" onClick={this.handlePoistu.bind(this)}>Poistu</button>
                                <button type="submit" className="btn btn-outline-success btn-lg" onClick={this.handleSubmit.bind(this)}>Tallenna</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>

        )
    }

}
function HelpModal() {
    return (
        <div className="modal fade" id="helpModal" tabIndex="-1" role="dialog" aria-labelledby="ModalLabel" aria-hidden="true">
            <div className="modal-dialog" role="document">
                <div className="modal-content">
                    <div className="modal-header">
                        <h5 className="modal-title" id="ModalLabel">Help</h5>
                        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div className="modal-body">
                        <p>Voit lisätä uuden muistilapun klikkaamalla lisää uusi -näppäintä.</p>
                        <p>Tallentamiasi muistilappuja voi muokata klikkaamalla muokkaa-tekstiä ja muistilapun voi poistaa kohdasta poista</p>
                    </div>
                    <div className="modal-footer">
                        <button type="button" className="btn btn-outline-secondary" data-dismiss="modal">Sulje</button>
                    </div>
                </div>
            </div>
        </div>
    );
}
